import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import empty from "@/components/empty";
import Json from '@/Json';
export default {
	components: {
		uniLoadMore,
		empty
	},
	data() {
		return {
			tabCurrentIndex: 0,
			navList: [{
					state: 0,
					text: '全部',
					loadingType: 'more',
					orderList: [],
					page: 0,
					pageSize: 10
				},
				{
					state: 1,
					text: '待付款',
					loadingType: 'more',
					orderList: [],
					page: 0,
					pageSize: 10
				},
				{
					state: 2,
					text: '待收货',
					loadingType: 'more',
					orderList: [],
					page: 0,
					pageSize: 10
				},
				{
					state: 3,
					text: '待评价',
					loadingType: 'more',
					orderList: [],
					page: 0,
					pageSize: 10
				},
				{
					state: 4,
					text: '售后',
					loadingType: 'more',
					orderList: [],
					page: 0,
					pageSize: 10
				}
			],
		};
	},
	
	onLoad(options){
		/**
		 * 修复app端点击除全部订单外的按钮进入时不加载数据的问题
		 * 替换onLoad下代码即可
		 */
		this.tabCurrentIndex = +options.state;




		if(options.state == 0){
			this.loadData()
		}

		
	},
	 
	methods: {
		//获取订单列表
		async loadData(source){

			//这里是将订单挂载到tab列表下
			let index = this.tabCurrentIndex;
			let navItem = this.navList[index];
			let state = navItem.state;
			let page = ++navItem.page;
			let pageSize = navItem.pageSize;
			
			if(source === 'tabChange' && navItem.loaded === true){
				//tab切换只有第一次需要加载数据
				return;
			}
			if(navItem.loadingType === 'loading' || navItem.loadingType === 'noMore'){
				//防止重复加载
				return;
			}
			
			navItem.loadingType = 'loading';
			
			
			let params = {
				status: state,
				page: page,
				pageSize
			}

			let { data: { docs, total} } = await this.Api.orderList(params);
			this.$set(navItem, 'total', total);
			docs = docs.filter(item=>{
					//添加不同状态下订单的表现形式
					item = Object.assign(item, this.orderStateExp(item.status));
					//演示数据所以自己进行状态筛选
					if(state === 0){
						//0为全部订单
						return item;
					}
					return item.status === state
				});
			docs.forEach(item=>{
				navItem.orderList.push(item);
			})
			// console.log(docs);
			// this.navList[index].orderList = navItem.orderList.concat(docs);
			if(this.navList[index].orderList.length == total) {
				navItem.loadingType = 'noMore';
			} else {
				navItem.loadingType = 'more';
			}
			this.$set(navItem, 'loaded', true);
			// setTimeout(()=>{
			// 	let orderList = Json.orderList.filter(item=>{
			// 		//添加不同状态下订单的表现形式
			// 		item = Object.assign(item, this.orderStateExp(item.state));
			// 		//演示数据所以自己进行状态筛选
			// 		if(state === 0){
			// 			//0为全部订单
			// 			return item;
			// 		}
			// 		return item.state === state
			// 	});
			// 	orderList.forEach(item=>{
			// 		navItem.orderList.push(item);
			// 	})
			// 	//loaded新字段用于表示数据加载完毕，如果为空可以显示空白页
			// 	this.$set(navItem, 'loaded', true);
				
			// 	//判断是否还有数据， 有改为 more， 没有改为noMore 
			// 	navItem.loadingType = 'more';
			// }, 600);
			
		}, 

		//swiper 切换
		changeTab(e){
			this.tabCurrentIndex = e.target.current;
			this.loadData('tabChange');
		},
		//顶部tab点击
		tabClick(index){
			this.tabCurrentIndex = index;
		},
		//删除订单
		deleteOrder(index){
			uni.showLoading({
				title: '请稍后'
			})
			setTimeout(()=>{
				this.navList[this.tabCurrentIndex].orderList.splice(index, 1);
				uni.hideLoading();
			}, 600)
		},
		//取消订单
		cancelOrder(item){
			uni.showLoading({
				title: '请稍后'
			})
			setTimeout(()=>{
				let {stateTip, stateTipColor} = this.orderStateExp(5);
				item = Object.assign(item, {
					status: 5,
					stateTip, 
					stateTipColor
				})
				
				//取消订单后删除待付款中该项
				let list = this.navList[1].orderList;
				let index = list.findIndex(val=>val.id === item.id);
				index !== -1 && list.splice(index, 1);
				
				uni.hideLoading();
			}, 600)
		},
		
		// 进入订单详情
		goOrderDetials(item) {
			let id = item._id;
			uni.navigateTo({
			    url: `/pages/order/orderDetails?id=${id}`
			});
		},

		//订单状态文字和颜色
		orderStateExp(state){
			let stateTip = '',
				stateTipColor = '#fa436a';
			switch(+state){
				case 1:
					stateTip = '待付款'; break;
				case 2:
					stateTip = '待发货'; break;
				case 3:
					stateTip = '待评论'; break;
				case 4:
					stateTip = '售后'; break;
				case 5:
					stateTip = '订单已关闭'; 
					stateTipColor = '#909399';
					break;
					
				//更多自定义
			}
			return {stateTip, stateTipColor};
		}
	},
}
